import React from 'react'
import invert from 'invert-color';


const da = () => {
  var theme_color = document.querySelector('meta[name="theme-color"]').getAttribute('content')
  var inverted_color = invert(theme_color);
  document.body.style.background = inverted_color
}

da()

function Luce() {
  return (
    <div>
      <p>Colored</p>
    </div>
  )
}

export default Luce